.cont_wallet_box{
    .container{

        .row{
            background-color: #28272e;
            border-radius: 10px;
        }
    }
}

.modal-backdrop.show{
    display: none;
}

.whitelistPopup{
    background-color: #28272e;
    text-align: center;
    color: #05f9bc;
}
.modal-content{
    border-radius: 15px;
    background-color: #28272e;

}
.modal-body{
    border-radius: 12px;
    background-color: #28272e;
    padding: 4rem;
}
.modal-header{
    background-color: #28272e;
    border-bottom: none;
    button{
        filter: invert(1);
    }
}