.collector_section {
    background-image: url("../../assets/Leaderboard\ background..png");
    background-size: cover;
    background-position: center;

    .collectors_Head {
        display: flex;
        flex-direction: column;
        align-items: center;

        img {
            width: 200px;
            margin-left: auto;
            margin-right: auto;
            margin-bottom: 20px;
        }
        h2 {
            text-transform: uppercase;
            color: #05f9bc;
            font-family: sans-serif;
            font-weight: 900;
            letter-spacing: 0px;
            margin-bottom: 20px;
        }
    }

    .collector_iframe {
        text-align: center;

        a{
            margin-right: 20px;
            background-color: #05f9bc;
            color: #19181e;
            border-radius: 5px;
            font-weight: 900;
            letter-spacing: 0px;
            font-size: 1.2em;
            padding: 5px 60px;
            border: none;
            margin-top: 20px;
        }
        iframe {
            display: block !important;
            width: 100%;
            height: 400px;
            border: 3px solid #05f9bc;
            border-radius: 5px;
            overflow-y: scroll;
            min-height: 400px;
            &::-webkit-scrollbar {
                width: 10px;
                background-color: #212028;
                border-radius: 25px;
            }
        
            &::-webkit-scrollbar-thumb {
                width: 10px;
                background-color: #04f9bc;
                border-radius: 25px;
            }
        }
    }
}

html::-webkit-scrollbar {
    width: 10px;
    background-color: #212028;
    border-radius: 25px;
}

html::-webkit-scrollbar-thumb {
    width: 10px;
    background-color: #04f9bc;
    border-radius: 25px;
}

@media screen and (max-width:525px){
    .collector_section{
        padding: 0px !important;
    }
    .collector_section .collector_iframe{
        margin-bottom: 20px;
    }
    .collector_section .collectors_Head{
        margin-top: 20px;
    }
    .collector_section .collectors_Head h2{
        text-align: center;
    }
    .collector_section .collector_iframe a{
        margin-right: 0%;
    }
}