@use "../abstracts/"as *;
// @font-face {
//   font-family: AvenirNext;
//   src: url(../../assets//font/AvenirNext.ttc);
// }


*,
*::before,
*::after {
  box-sizing: border-box;
}

iframe {
  display: none !important;
}

body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  // font-family: "Rubik", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
  //   "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  font-family: 'Manrope', sans-serif;
  // font-family: AvenirNext;
  margin: 0;
  letter-spacing: 1px;
  word-spacing: 1px;
}

ul[role="list"],
ol[role="list"] {
  list-style: none;
}

html {
  scroll-behavior: smooth;
}

html:focus-within {
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  color: $dark-theme-text;
  background: #181820 !important;
  // background-image: url('../../assets/background.png');
  // background-repeat: no-repeat;
}

a {
  text-decoration: none;
  color: inherit;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img,
picture {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}


// ====================== Rewards ==========================

.reawardsHeroSection {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 164px 100px;
  background-image: url('../../assets/backgroun1d.png');
  background-repeat: no-repeat;

  .reawardsHeroDiv {

    .title {
      font-size: 2em;
    }

    .numberHead {
      font-size: 4em;
      font-weight: 900;
      background: linear-gradient(68deg, #00ff1f 3%, #0f999c 36%, #0b4e4f 77%, #0b1212 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }
}


.intro-section {

  .image-container {
    width: 100%;
    display: flex;
    justify-content: center;

    img {
      width: 340px;
      height: auto;
    }
  }
}


// ====================== Rarity ==========================


.rarity-section {

  .image-container {

    img {
      padding: 20px;
    }
  }

}


.stakeDiv {

  .accordion button:hover::after {
    border: none;
  }

  .accordion button::after {
    filter: brightness(0.5) invert(1);
  }

  // .accordion button
  .accordion button:focus::after {
    border: none;
    filter: brightness(0.5) invert(1);
  }

  .accordion-header {

    button {
      font-size: .7em !important;
      color: #fff;
      display: flex;
    }

    button:focus {
      box-shadow: none;
    }
  }

  .accordion-item {
    background-color: #0b1212;
  }

  .customTab {
    border-bottom: 0px !important;

    Button {
      padding: 5px 50px;
      margin: 42px 0px;
      border-radius: 0px;
      font-weight: bold;
      border: 2px solid #fff;
      color: #fff !important;
    }

    Button.active {
      padding: 5px 50px;
      margin: 42px 0px;
      border-radius: 0px;
      font-weight: bold;
      border: 2px solid #fff;
      color: #000 !important;
      background-color: #fff;
    }
  }

  .stakeContentDiv {

    .inputDiv {
      display: flex;
      height: 50px;

      button {
        color: black;
        font-weight: 900;
        background: #0e8d8f;
        text-align: center;
        width: 100px;
        font-size: 1em !important;
        padding: 0px 9px;
        letter-spacing: normal;
        border-radius: 0px !important;
        border: 1px solid #085b5c;
      }

      .maxBtn {
        color: #cdcdcd;
        background-color: #121e1d;
        border: none;
        border-bottom: 1px solid #1b4d4e;
      }

      input {
        width: 100%;
        background: #0b1212;
        border: none;
        border-bottom: 1px solid #1b4d4e;
        color: #fff;
        -moz-appearance: textfield;

        &::-webkit-inner-spin-button {
          display: none;
        }
      }

      input::placeholder {
        color: #fff;
      }

      input:focus-visible {
        border: none;
        outline: none;
        border-bottom: 1px solid #0e8d8f;
      }

    }

    .ContentDiv {
      margin-top: 20px;

      .StakewalletDiv,
      .YourStakeDiv {
        display: flex;
        justify-content: space-between;
      }


    }


  }
}

.rewardsCollectTop {
  display: flex;
  justify-content: end;
}

.whats_this {
  padding: 20px;
  color: #5f8585;
  font-weight: 700;
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
}

.compoundingDiv {
  display: flex;
  align-items: center;
  color: #5f8585;
  font-weight: 700;
  position: relative;
  cursor: pointer;
}

.whats_this_popover {
  position: absolute;
  width: 457px;
  bottom: -115px;
  z-index: 5;
  right: 0px;
  background-color: #fff;
  padding: 10px;
  border: 1px solid #0b4e4f;
  color: #0b1212;
  font-size: 1em;
  letter-spacing: 0.1px;
  font-weight: 500;
  display: none;
}

.whats_this:hover .whats_this_popover {
  display: block;
}


.compounding_popover {
  position: absolute;
  width: 457px;
  bottom: -115px;
  z-index: 5;
  right: 0px;
  background-color: #fff;
  padding: 10px;
  border: 1px solid #0b4e4f;
  color: #0b1212;
  font-size: 1em;
  letter-spacing: 0.1px;
  font-weight: 500;
  display: none;
}

.compoundingDiv:hover .compounding_popover {
  display: block;
}


.stakeTitle {
  margin-bottom: 20px;

  h2 {
    font-weight: 900;
  }
}

