.cntwllt_nav {


  .nav-link.active {
    color: #495057;
    background-color: #0b1212;
    box-shadow: inset 0px 0px 14px #067778;
    border: none;
    border-radius: 0px;
  }

  .nav-link:hover {
    border: none;
    box-shadow: inset 0px 0px 12px #067778;
    border-radius: 0px;
    color: transparent !important;
  }

  .nav-link:focus {
    border: none;
  }

  .nav-link:focus-visible {
    border: none;
  }

  .nav-tabs {
    border: none;
    background: #0b1212;
    border-radius: 0px;
    margin: 30px 0px;

    .nav-link {
      border: none;
    }
  }
}

.searchDHT {
  color: #fff;
  background: #181820;
  border: none !important;
  border-top-right-radius: 10px !important; 
  border-bottom-right-radius: 10px !important;
  // border-bottom: 1px solid #1b4d4e !important;
}

.searchDHT:focus {
  color: #fff;
  background: #181820;
  border: none !important;
  // border-bottom: 1px solid #0e8d8f !important;
