}

.form-check-input {
  background-color: #0b1212;
  // border-color: #067778;
  width: 30px;
  height: 30px;
  border-radius: 50% !important;
  box-shadow: inset 0px 0px 4px #067778d6;
}

.form-check-input:focus {
