#custDrop_ul_ID {
  background-color: #fff;
  color: #000;
  padding: 10px;
  list-style-type: none;
  line-height: 3;
  height: 100px;
  overflow-y: scroll;

  .select_all_unstakeBtn{
    padding: 5px 20px;
    width: auto;
    line-height: 1;
    background-color: #0e8c8e;
    color: #fff;
    float: right;
    transition: all .2s;

    &:hover{
      transform: scale(.95);
    }
  }
}

.formDiv_dropdown {
  display: flex;
  align-items: center;
}

#custDrop_ul_ID li input {
  margin-top: 0px;
