.vibeMap_section {
    background-image: url("../../assets/VIBE_Map__Background.png");
    // background-size: cover;
    background-repeat: no-repeat;
    background-position-x: 80%;

    .container {
        max-width: 1140px;
        display: block;

        .vibeMap_head {
            h2 {
                text-transform: uppercase;
                font-size: 2.5em;
                color: #05f9bc;
                font-family: sans-serif;
                font-weight: 900;
                text-align: center;
            }
        }

        .vibe_phaseDiv {
            position: relative;
            background-color: #3d3c41;
            padding: 20px;
            border-radius: 10px;
            min-height: 190px;

            .vibe_phaseDiv_barX {
                content: "";
                position: absolute;
                top: 50%;
                right: 0%;
                width: 100px;
                height: 10px;
                background-color: #05f9bc;
                transform: translateX(60px);
                z-index: -1;
            }
            .vibe_phaseDiv_barY {
                content: "";
                position: absolute;
                top: 50%;
                right: 50%;
                width: 10px;
                height: 120px;
                background-color: #05f9bc;
                transform: translateY(60px);
                z-index: -1;
            }
            h6 {
                color: #05f9bc;
                letter-spacing: 0px;
                font-family: sans-serif;
                font-weight: 600;
                font-size: 12px;
                text-transform: uppercase;
            }
            h4 {
            }
            p {
                color: #ffffffb5;
                font-size: 11px;
            }
            .vibe_site_logo {
                display: flex;
                justify-content: flex-end;
                position: absolute;
                bottom: 3%;
                right: 2%;

                img {
                    width: 40px;
                }
            }
            .vibe_status_div {
                width: 40px;
                height: 40px;
                position: absolute;
                top: -20px;
                right: -20px;
                svg {
                    width: 100%;
                    height: auto;
                    color: #05f9bc;
                }
            }

            // &::after{
            //     content: '';
            //     position: absolute;
            //     top: 50%;
            //     right: 0%;
            //     width: 100%;
            //     height: 10px;
            //     background-color: #05f9bc;
            // }
        }
    }
}

@media screen and (max-width: 991px) {
    .vibe_phaseDiv_barX,
    .vibe_phaseDiv_barY {
        display: none;
    }
}
