.about_gvo_section {
    background-color: #1f1f23;

    .container {
        max-width: 1140px;
        display: block;

        .about_gvo_contentDiv {
            display: flex;
            align-items: center;
            height: 100%;

            .about_gvo_content {
                h2 {
                    text-transform: uppercase;
                    font-size: 2.5em;
                    color: #05f9bc;
                    font-family: sans-serif;
                    font-weight: 900;
                }

                p {
                    margin-bottom: 20px;
                    color: #ffffffb5;
                    font-size: 14px;
                }

                b {
                    color: #05f9bc;
                    letter-spacing: 0px;
                }
                ul{
                    li{
                        color: #05f9bc;
                    }
                }
            }
        }

        .about_gvo_benefit_contentDiv_container {
            display: flex;
            align-items: center;
            height: 100%;

            .about_gvo_benefit_contentDiv {
                h3 {
                    text-transform: uppercase;
                    font-size: 1.5em;
                    color: #05f9bc;
                    font-family: sans-serif;
                    font-weight: bold;
                }

                ul {
                    // list-style-type: none;
                    // padding: 0px;

                    li {
                        margin-bottom: 5px;
                        color: #ffffffb5;
                        font-size: 14px;
                    }
                }
                b {
                    color: #05f9bc;
                    letter-spacing: 0px;
                }
                p {
                    letter-spacing: 0px;
                }
                a {
                    background-color: #05f9bc;
                    color: #19181e;
                    border-radius: 5px;
                    font-weight: 900;
                    letter-spacing: 0px;
                    font-size: 1.2em;
                    padding: 5px 60px;
                    border: none;
                    margin-top: 20px;
                    line-height: 4;
                }
            }
        }
    }
}
@media screen and (max-width:768px){
    .about_gvo_section .container .row:nth-child(1){
        flex-direction: column-reverse;
    }

    .about_gvo_imgDiv{
        width: 100%;
        img{
            width: 200px;
            margin-left: auto;
            margin-right: auto;
            margin-bottom: 20px;
        }
    } 

    .about_gvo_benefit_divImg{

        img{
            width: 200px;
            margin-left: auto;
            margin-right: auto;
            margin-bottom: 20px;
        }
    }
}

@media screen and (max-width:425px){
    .about_gvo_section .container .about_gvo_contentDiv .about_gvo_content h2{
        font-size: 2em;
    }

    .about_gvo_imgDiv{
        padding: 20px;
    }

    .about_gvo_section .container{
        padding: 0px;
    }

    .about_gvo_benefit_divImg{
        margin: 20px 0px;
    }
    .about_gvo_section .container .about_gvo_benefit_contentDiv_container .about_gvo_benefit_contentDiv a{
        padding: 5px 15px;
    }
}