.our_story_section{
    padding: 8em 0em !important;
    background-image: linear-gradient(#191921e5, #191921e5), url('../../assets/DH-preview-PANEL.png');
    background-size: cover;


    .our_story_img{

        .carousel {

            .carousel-indicators{
                display: none;
            }
            .carousel-control-prev{
                // display: none;
            }
            .carousel-control-next{
                // display: none;
            }
        }
    }

}

.our_story_content_container{
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;

        .our_story_contentDiv{

            h2{
                text-transform: uppercase;
                font-size: 2.5em;
                color: #05f9bc;
                font-family: sans-serif;
                font-weight: 900;
            }
            p{
                margin-bottom: 20px;
                color: #ffffffb5;
                font-size: 14px;
            }
        }
}

@media screen and (max-width:425px){
    .our_story_content_container .our_story_contentDiv h2{
        font-size: 2em;
    }
    .our_story_contentDiv{
        margin-top: 20px;
    }
}